<template>
  <div id="app">
    <Backg />
    <Header />
    <Content />  
  </div>
</template>

<script>
import Backg from './components/Backg'
import Header from './components/Header'
import Content from './components/Content'

export default {
  name: 'App',
  components: {
    Backg,
    Header,
    Content,
  }
}
</script>

<style lang="stylus" scoped>
#app {
  width 100%
  height 100%
  text-align: center;
  color: #2c3e50;
}
</style>




