<template>
    <div id="msg">
        <Msgname v-model="msgname" />
        <Msgtext v-model="msgtext" ref="msg" />
        <input @click="msgSend" type="button" class="btn" value="发布"/>
        <Msglist :list="list" @reply="handleReply" />
    </div>
</template>

<script>
import Msglist from './Msglist'
import Msgname from './Msgname'
import Msgtext from './Msgtext'

export default {
    data(){
        return{
            msgname: '',
            msgtext: '',
            list: []
        }
    },

    components: {
        Msgname,
        Msgtext,
        Msglist
    },

    methods: {
        //发布留言
        msgSend: function(){
            if (this.msgname=== '') {
                window.alert('请输入您的名字')
                return
            }
            if (this.msgtext === '') {
                window.alert('请输入留言内容')
                return
            }
            // 数组list存储了所有的留言内容，通过函数给list添加一项留言数据，添加成功后把文本框置空
            this.list.push({
                msgname: this.msgname,
                msgtext: this.msgtext
            })
            this.msgtext = ''
        },
        //回复留言
        handleReply: function(index){
            var msgname = this.list[index].msgname
            this.msgtext = '回复@' + msgname + ':'
            this.$refs.msgtext.focus()
        }      
    }

}
</script>


<style lang="stylus" scoped>
#msg{
    width 100%
    height 100%
    position absolute
    top 100px
}


</style>



