<template>
    <div id="content">
        <div class="nav">
            <ul class="nav-list">
            <a  class="here" @click="toHome"><li><img src="@/assets/images/主页.png" /> 首 页</li></a>
            <a  @click="toPhoto"><li><img src="@/assets/images/照片.png" /> 杂 录</li></a>
            <a  @click="toMsg"><li><img src="@/assets/images/留言.png" /> 留 言</li></a>
            <a  @click="toAbout"><li><img src="@/assets/images/关于.png" /> 关 于</li></a>
            </ul>
        </div>
        <router-view />
    </div>

</template>

<script>

export default{
  methods:{
    toHome:function(){
      this.$router.push({name: 'Home'})
    },
    toPhoto:function(){
      this.$router.push({name: 'Photo'})
    },
    toMsg:function(){
      this.$router.push({name: 'Message'})
    },
    toAbout:function(){
      this.$router.push({name: 'About'})
    },
  }
}

</script>

<style lang="stylus" scoped>
#content{
    position absolute
    top 300px
    left 10%
    height 100%
    width 80%
}
.nav{
    width 100%
    position absolute
    left 0
    font-size 20px
    border-bottom solid
    border-width 1px
    border-color lightgray
    .nav-list{
        width 100%
        margin-left 20%
        list-style none
        background rgba(255, 255, 255, .8)
    }
    .nav-list a{
        float left
        margin 0 30px
        text-decoration none
        cursor pointer
    }
    .nav-list li a :hover{
        background-color gray
    }
}
</style>


