<template>
    <div id="header">
        <Music />
        <Avatar />
    </div>   
 </template>

<script>
import Avatar from './header/Avatar'
import Music from './header/Music'

export default{
    components: {
        Avatar,
        Music,
    }
}
</script>

<style lang="stylus" scoped>
#header{
    height 300px
    width 100%
    position absolute
    top 0
}
</style>




