import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/components/content/home/Home'
import Photo from '@/components/content/Photo'
import Message from '@/components/content/message/Message'
import About from '@/components/content/About'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/photo',
      name: 'Photo',
      component: Photo
    },
    {
      path: '/message',
      name: 'Message',
      component: Message
    },
    {
      path: '/about',
      name: 'About',
      Component: About
    }
  ]
})



