<template>
    <div id="Msglist" >
        <div v-if="this.list.length">
            <div v-for="(item, index) in list" :key="index" class="list-item">
                <span class="list-name" >{{item.msgname}} : </span>
                <span class="list-text" >{{item.msgtext}}</span>
                <a @click="listChange(index)">回复</a>
            </div>
        </div>
        <div v-if="!this.list.length" class="list-nothing">
            留言列表为空
        </div>
    </div>
</template>
<script>
export default {
  props: {
    list: {
      type: Array,
      default: function () {
        return []
      }
    }
  },
  methods: {
    listChange: function (index) {
      this.handleReply(index)
    },
    handleReply: function (index) {
      this.$emit('reply', index)
    }
  }
}
</script>


<style lang="stylus" scoped>
.list-item,
.list-nothing{
    margin 30px auto
}
.list-item{
    .list-name{
        
        font-size 19px
        color blue
    }
    .list-text{
    }
    a{
        float right
        margin-right 30%
        cursor pointer
    }
    a:hover{
        color gray
    }
}
</style>




