<template>
    <div id="Msgtext">
        <span>留言内容</span>
        <textarea rows="5" cols="30" type="text" :ref="message" :value="value" @input="updateText" placeholder="要说些什么呢？" />
    </div>
</template>

<script>
export default {

// 接收父组件传入的value
  props: {
    value: {
      type: String,
      default: ''
    }
  },

  methods: {
    // 监听input值
    updateText: function (event) {
      this.$emit('input', event.target.value)
    },
    focus: function () {
      // 一个对象，持有注册过ref特性的所有DOM元素和组件实例。
      this.$refs.msg.focus()
    }
  }

}
</script>

<style lang="stylus" scoped>
#Msgtext{
    margin 20px auto
    textarea{
        resize none
    }
}
</style>



