<template>
    <div id="Msgname">
        <span>名称</span>
        <input type="text" :value="value" @input="updateName" placeholder="要怎么称呼呢？"/>
    </div>
</template>

<script>
export default {

// 接收父组件传过来的value
  props: {
    value: {
      type: [String, Number],
      default: ''
    }
  },

  methods: {
    // 监听input输入事件
    updateName: function (event) {
      // 触发父组件的自定义事件
      // target 事件属性可返回事件的目标节点（触发该事件的节点），如生成事件的元素、文档或窗口。
      // 将input的value值传给父组件
      this.$emit('input', event.target.value)
    }
  }
}

</script>


<style lang="stylus" scoped>
#Msgname{
    width 100%
    margin 20px auto

}
</style>



